f DSGVO (Berechtigte Interessen) ")])])])]),_c('h3',{staticClass:"adsimple-111879084"},[_vm._v("Was ist Google Maps?")]),_c('p',[_vm._v(" Wir benützen auf unserer Website Google Maps der Firma Google Inc. Für den europäischen Raum ist das Unternehmen Google Ireland Limited (Gordon House, Barrow Street Dublin 4, Irland) für alle Google-Dienste verantwortlich. Mit Google Maps können wir Ihnen Standorte besser zeigen und damit unser Service an Ihre Bedürfnisse anpassen. Durch die Verwendung von Google Maps werden Daten an Google übertragen und auf den Google-Servern gespeichert. Hier wollen wir nun genauer darauf eingehen, was Google Maps ist, warum wir diesen Google-Dienst in Anspruch nehmen, welche Daten gespeichert werden und wie Sie dies unterbinden können. ")]),_c('p',[_vm._v(" Google Maps ist ein Internet-Kartendienst der Firma Google. Mit Google Maps können Sie online über einen PC, ein Tablet oder eine App genaue Standorte von Städten, Sehenswürdigkeiten, Unterkünften oder Unternehmen suchen. Wenn Unternehmen auf Google My Business vertreten sind, werden neben dem Standort noch weitere Informationen über die Firma angezeigt. Um die Anfahrtsmöglichkeit anzuzeigen, können Kartenausschnitte eines Standorts mittels HTML-Code in eine Website eingebunden werden. Google Maps zeigt die Erdoberfläche als Straßenkarte oder als Luft- bzw. Satellitenbild. Dank der Street View Bilder und den hochwertigen Satellitenbildern sind sehr genaue Darstellungen möglich. ")]),_c('h3',{staticClass:"adsimple-111879084"},[_vm._v(" Warum verwenden wir Google Maps auf unserer Website? ")]),_c('p',[_vm._v(" All unsere Bemühungen auf dieser Seite verfolgen das Ziel, Ihnen eine nützliche und sinnvolle Zeit auf unserer Webseite zu bieten. Durch die Einbindung von Google Maps können wir Ihnen die wichtigsten Informationen zu diversen Standorten liefern. Sie sehen auf einen Blick wo wir unseren Firmensitz haben. Die Wegbeschreibung zeigt Ihnen immer den besten bzw. schnellsten Weg zu uns. Sie können den Anfahrtsweg für Routen mit dem Auto, mit öffentlichen Verkehrsmitteln, zu Fuß oder mit dem Fahrrad abrufen. Für uns ist die Bereitstellung von Google Maps Teil unseres Kundenservice. ")]),_c('h3',{staticClass:"adsimple-111879084"},[_vm._v(" Welche Daten werden von Google Maps gespeichert? ")]),_c('p',[_vm._v(" Damit Google Maps ihren Dienst vollständig anbieten kann, muss das Unternehmen Daten von Ihnen aufnehmen und speichern. Dazu zählen unter anderem die eingegebenen Suchbegriffe, Ihre IP-Adresse und auch die Breiten- bzw. Längenkoordinaten. Benutzen Sie die Routenplaner-Funktion wird auch die eingegebene Startadresse gespeichert. Diese Datenspeicherung passiert allerdings auf den Webseiten von Google Maps. Wir können Sie darüber nur informieren, aber keinen Einfluss nehmen. Da wir Google Maps in unsere Webseite eingebunden haben, setzt Google mindestens ein Cookie (Name: NID) in Ihrem Browser. Dieses Cookie speichert Daten über Ihr Userverhalten. Google nutzt diese Daten in erster Linie, um eigene Dienste zu optimieren und individuelle, personalisierte Werbung für Sie bereitzustellen. ")]),_c('p',[_vm._v(" Folgendes Cookie wird aufgrund der Einbindung von Google Maps in Ihrem Browser gesetzt: ")]),_c('p',[_c('b',{staticClass:"adsimple-111879084"},[_vm._v("Name:")]),_vm._v(" NID"),_c('br'),_c('b',{staticClass:"adsimple-111879084"},[_vm._v("Wert:")]),_vm._v(" 188=h26c1Ktha7fCQTx8rXgLyATyITJ111879084-5"),_c('br'),_c('b',{staticClass:"adsimple-111879084"},[_vm._v("Verwendungszweck:")]),_vm._v(" NID wird von Google verwendet, um Werbeanzeigen an Ihre Google-Suche anzupassen. Mit Hilfe des Cookies „erinnert“ sich Google an Ihre am häufigsten eingegebenen Suchanfragen oder Ihre frühere Interaktion mit Anzeigen. So bekommen Sie immer maßgeschneiderte Werbeanzeigen. Das Cookie enthält eine einzigartige ID, die Google benutzt, um Ihre persönlichen Einstellungen für Werbezwecke zu sammeln."),_c('br'),_c('b',{staticClass:"adsimple-111879084"},[_vm._v("Ablaufdatum:")]),_vm._v(" nach 6 Monaten ")]),_c('p',[_c('b',{staticClass:"adsimple-111879084"},[_vm._v("Anmerkung:")]),_vm._v(" Wir können bei den Angaben der gespeicherten Daten keine Vollständigkeit gewährleisten. Speziell bei der Verwendung von Cookies sind Veränderungen nie auszuschließen. Um das Cookie NID zu identifizieren, wurde eine eigene Testseite angelegt, wo ausschließlich Google Maps eingebunden war. ")]),_c('h3',{staticClass:"adsimple-111879084"},[_vm._v(" Wie lange und wo werden die Daten gespeichert? ")]),_c('p',[_vm._v(" Die Google-Server stehen in Rechenzentren auf der ganzen Welt. Die meisten Server befinden sich allerdings in Amerika. Aus diesem Grund werden Ihre Daten auch vermehrt in den USA gespeichert. Hier können Sie genau nachlesen wo sich die Google-Rechenzentren befinden: "),_c('a',{staticClass:"adsimple-111879084",attrs:{"href":"https://www.google.com/about/datacenters/inside/locations/?hl=de","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("https://www.google.com/about/datacenters/inside/locations/?hl=de")])]),_c('p',[_vm._v(" Die Daten verteilt Google auf verschiedenen Datenträgern. Dadurch sind die Daten schneller abrufbar und werden vor etwaigen Manipulationsversuchen besser geschützt. Jedes Rechenzentrum hat auch spezielle Notfallprogramme. Wenn es zum Beispiel Probleme bei der Google-Hardware gibt oder eine Naturkatastrophe die Server lahm legt, bleiben die Daten ziemlich sicher trotzdem geschützt. ")]),_c('p',[_vm._v(" Manche Daten speichert Google für einen festgelegten Zeitraum. Bei anderen Daten bietet Google lediglich die Möglichkeit, diese manuell zu löschen. Weiters anonymisiert das Unternehmen auch Informationen (wie zum Beispiel Werbedaten) in Serverprotokollen, indem es einen Teil der IP-Adresse und Cookie-Informationen nach 9 bzw.18 Monaten löscht. ")]),_c('h3',{staticClass:"adsimple-111879084"},[_vm._v(" Wie kann ich meine Daten löschen bzw. die Datenspeicherung verhindern? ")]),_c('p',[_vm._v(" Mit der 2019 eingeführten automatischen Löschfunktion von Standort- und Aktivitätsdaten werden Informationen zur Standortbestimmung und Web-/App-Aktivität – abhängig von Ihrer Entscheidung – entweder 3 oder 18 Monate gespeichert und dann gelöscht. Zudem kann man diese Daten über das Google-Konto auch jederzeit manuell aus dem Verlauf löschen. Wenn Sie Ihre Standorterfassung vollständig verhindern wollen, müssen Sie im Google-Konto die Rubrik „Web- und App-Aktivität“ pausieren. Klicken Sie „Daten und Personalisierung“ und dann auf die Option „Aktivitätseinstellung“. Hier können Sie die Aktivitäten ein- oder ausschalten. ")]),_c('p',[_vm._v(" In Ihrem Browser können Sie weiters auch einzelne Cookies deaktivieren, löschen oder verwalten. Je nach dem welchen Browser Sie verwenden, funktioniert dies immer etwas anders. Die folgenden Anleitungen zeigen, wie Sie Cookies in Ihrem Browser verwalten: ")]),_c('p',[_c('a',{staticClass:"adsimple-111879084",attrs:{"href":"https://support.google.com/chrome/answer/95647?tid=111879084","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Chrome: Cookies in Chrome löschen, aktivieren und verwalten")])]),_c('p',[_c('a',{staticClass:"adsimple-111879084",attrs:{"href":"https://support.apple.com/de-at/guide/safari/sfri11471/mac?tid=111879084","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Safari: Verwalten von Cookies und Websitedaten mit Safari")])]),_c('p',[_c('a',{staticClass:"adsimple-111879084",attrs:{"href":"https://support.mozilla.org/de/kb/cookies-und-website-daten-in-firefox-loschen?tid=111879084","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Firefox: Cookies löschen, um Daten zu entfernen, die Websites auf Ihrem Computer abgelegt haben")])]),_c('p',[_c('a',{staticClass:"adsimple-111879084",attrs:{"href":"https://support.microsoft.com/de-at/help/17442/windows-internet-explorer-delete-manage-cookies?tid=111879084","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Internet Explorer: Löschen und Verwalten von Cookies")])]),_c('p',[_c('a',{staticClass:"adsimple-111879084",attrs:{"href":"https://support.microsoft.com/de-at/help/4027947/windows-delete-cookies?tid=111879084","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Microsoft Edge: Löschen und Verwalten von Cookies")])]),_c('p',[_vm._v(" Falls Sie grundsätzlich keine Cookies haben wollen, können Sie Ihren Browser so einrichten, dass er Sie immer informiert, wenn ein Cookie gesetzt werden soll. So können Sie bei jedem einzelnen Cookie entscheiden, ob Sie es erlauben oder nicht. ")]),_c('p',[_vm._v(" Bitte beachten Sie, dass bei der Verwendung dieses Tools Daten von Ihnen auch außerhalb der EU gespeichert und verarbeitet werden können. Die meisten Drittstaaten (darunter auch die USA) gelten nach derzeitigem europäischen Datenschutzrecht als nicht sicher. Daten an unsichere Drittstaaten dürfen also nicht einfach übertragen, dort gespeichert und verarbeitet werden, sofern es keine passenden Garantien (wie etwa EU-Standardvertragsklauseln) zwischen uns und dem außereuropäischen Dienstleister gibt. ")]),_c('h3',{staticClass:"adsimple-111879084"},[_vm._v("Rechtsgrundlage")]),_c('p',[_vm._v(" Wenn Sie eingewilligt haben, dass Google Maps eingesetzt werden darf, ist die Rechtsgrundlage der entsprechenden Datenverarbeitung diese Einwilligung. Diese Einwilligung stellt laut"),_c('b',{staticClass:"adsimple-111879084"},[_vm._v(" Art. 6 Abs. 1 lit. a DSGVO (Einwilligung)")]),_vm._v(" die Rechtsgrundlage für die Verarbeitung personenbezogener Daten, wie sie bei der Erfassung durch Google Maps vorkommen kann, dar. ")]),_c('p',[_vm._v(" Von unserer Seite besteht zudem ein berechtigtes Interesse, Google Maps zu verwenden, um unser Online-Service zu optimieren. Die dafür entsprechende Rechtsgrundlage ist "),_c('b',{staticClass:"adsimple-111879084"},[_vm._v("Art. 6 Abs. 1 lit. f DSGVO (Berechtigte Interessen)")]),_vm._v(". Wir setzen Google Maps gleichwohl nur ein, soweit Sie eine Einwilligung erteilt haben. ")]),_c('p',[_vm._v(" Google verarbeitet Daten von Ihnen u.a. auch in den USA. Wir weisen darauf hin, dass nach Meinung des Europäischen Gerichtshofs derzeit kein angemessenes Schutzniveau für den Datentransfer in die USA besteht. Dies kann mit verschiedenen Risiken für die Rechtmäßigkeit und Sicherheit der Datenverarbeitung einhergehen. ")]),_c('p',[_vm._v(" Als Grundlage der Datenverarbeitung bei Empfängern mit Sitz in Drittstaaten (außerhalb der Europäischen Union, Island, Liechtenstein, Norwegen, also insbesondere in den USA) oder einer Datenweitergabe dorthin verwendet Google sogenannte Standardvertragsklauseln (= Art. 46. Abs. 2 und 3 DSGVO). Standardvertragsklauseln (Standard Contractual Clauses – SCC) sind von der EU-Kommission bereitgestellte Mustervorlagen und sollen sicherstellen, dass Ihre Daten auch dann den europäischen Datenschutzstandards entsprechen, wenn diese in Drittländer (wie beispielsweise in die USA) überliefert und dort gespeichert werden. Durch diese Klauseln verpflichtet sich Google, bei der Verarbeitung Ihrer relevanten Daten, das europäische Datenschutzniveau einzuhalten, selbst wenn die Daten in den USA gespeichert, verarbeitet und verwaltet werden. Diese Klauseln basieren auf einem Durchführungsbeschluss der EU-Kommission. Sie finden den Beschluss und die entsprechenden Standardvertragsklauseln u.a. hier: "),_c('a',{staticClass:"adsimple-111879084",attrs:{"href":"https://eur-lex.europa.eu/eli/dec_impl/2021/914/oj?locale=de","target":"_blank","rel":"noopener"}},[_vm._v("https://eur-lex.europa.eu/eli/dec_impl/2021/914/oj?locale=de")])]),_c('p',[_vm._v(" Die Google Ads Datenverarbeitungsbedingungen (Google Ads Data Processing Terms), welche den Standardvertragsklauseln entsprechen, finden Sie unter "),_c('a',{staticClass:"adsimple-111879084",attrs:{"href":"https://business.safety.google/adsprocessorterms/","target":"_blank","rel":"noopener"}},[_vm._v("https://business.safety.google/adsprocessorterms/")]),_vm._v(". ")]),_c('p',[_vm._v(" Wenn Sie mehr über die Datenverarbeitung von Google erfahren wollen, empfehlen wir Ihnen die hauseigene Datenschutzerklärung des Unternehmens unter "),_c('a',{staticClass:"adsimple-111879084",attrs:{"href":"https://policies.google.com/privacy?hl=de","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("https://policies.google.com/privacy?hl=de")]),_vm._v(". ")]),_c('h2',{staticClass:"adsimple-111879084",attrs:{"id":"google-recaptcha-datenschutzerklaerung"}},[_vm._v(" Google reCAPTCHA Datenschutzerklärung ")]),_c('table',{attrs:{"border":"1","cellpadding":"15"}},[_c('tbody',[_c('tr',[_c('td',[_c('b',{staticClass:"adsimple-111879084"},[_vm._v("Google reCAPTCHA Datenschutzerklärung Zusammenfassung")]),_c('br'),_vm._v(" 👥 Betroffene: Besucher der Website"),_c('br'),_vm._v(" 🤝 Zweck: Optimierung unserer Serviceleistung und Schutz vor Cyberangriffen"),_c('br'),_vm._v(" 📓 Verarbeitete Daten: Daten wie etwa IP-Adresse, Browserinformationen, Ihr Betriebssystem, eingeschränkte Standorts- und Nutzungsdaten"),_c('br'),_vm._v(" Mehr Details dazu finden Sie weiter unten in dieser Datenschutzerklärung."),_c('br'),_vm._v(" 📅 Speicherdauer: abhängig von den gespeicherten Daten"),_c('br'),_vm._v(" ⚖️ Rechtsgrundlagen: Art. 6 Abs. 1 lit. a DSGVO (Einwilligung), Art. 6 Abs. 1 lit. f DSGVO (Berechtigte Interessen) ")])])])]),_c('h3',{staticClass:"adsimple-111879084"},[_vm._v("Was ist reCAPTCHA?")]),_c('p',[_vm._v(" Unser oberstes Ziel ist es, unsere Webseite für Sie und für uns bestmöglich zu sichern und zu schützen. Um das zu gewährleisten, verwenden wir Google reCAPTCHA der Firma Google Inc. Für den europäischen Raum ist das Unternehmen Google Ireland Limited (Gordon House, Barrow Street Dublin 4, Irland) für alle Google-Dienste verantwortlich. Mit reCAPTCHA können wir feststellen, ob Sie auch wirklich ein Mensch aus Fleisch und Blut sind und kein Roboter oder eine andere Spam-Software. Unter Spam verstehen wir jede, auf elektronischen Weg, unerwünschte Information, die uns ungefragter Weise zukommt. Bei den klassischen CAPTCHAS mussten Sie zur Überprüfung meist Text- oder Bildrätsel lösen. Mit reCAPTCHA von Google müssen wir Sie meist nicht mit solchen Rätseln belästigen. Hier reicht es in den meisten Fällen, wenn Sie einfach ein Häkchen setzen und so bestätigen, dass Sie kein Bot sind. Mit der neuen Invisible reCAPTCHA Version müssen Sie nicht mal mehr ein Häkchen setzen. Wie das genau funktioniert und vor allem welche Daten dafür verwendet werden, erfahren Sie im Verlauf dieser Datenschutzerklärung. ")]),_c('p',[_vm._v(" reCAPTCHA ist ein freier Captcha-Dienst von Google, der Webseiten vor Spam-Software und den Missbrauch durch nicht-menschliche Besucher schützt. Am häufigsten wird dieser Dienst verwendet, wenn Sie Formulare im Internet ausfüllen. Ein Captcha-Dienst ist eine Art automatischer Turing-Test, der sicherstellen soll, dass eine Handlung im Internet von einem Menschen und nicht von einem Bot vorgenommen wird. Im klassischen Turing-Test (benannt nach dem Informatiker Alan Turing) stellt ein Mensch die Unterscheidung zwischen Bot und Mensch fest. Bei Captchas übernimmt das auch der Computer bzw. ein Softwareprogramm. Klassische Captchas arbeiten mit kleinen Aufgaben, die für Menschen leicht zu lösen sind, doch für Maschinen erhebliche Schwierigkeiten aufweisen. Bei reCAPTCHA müssen Sie aktiv keine Rätsel mehr lösen. Das Tool verwendet moderne Risikotechniken, um Menschen von Bots zu unterscheiden. Hier müssen Sie nur noch das Textfeld „Ich bin kein Roboter“ ankreuzen bzw. bei Invisible reCAPTCHA ist selbst das nicht mehr nötig. Bei reCAPTCHA wird ein JavaScript-Element in den Quelltext eingebunden und dann läuft das Tool im Hintergrund und analysiert Ihr Benutzerverhalten. Aus diesen Useraktionen berechnet die Software einen sogenannten Captcha-Score. Google berechnet mit diesem Score schon vor der Captcha-Eingabe wie hoch die Wahrscheinlichkeit ist, dass Sie ein Mensch sind. reCAPTCHA bzw. Captchas im Allgemeinen kommen immer dann zum Einsatz, wenn Bots gewisse Aktionen (wie z.B. Registrierungen, Umfragen usw.) manipulieren oder missbrauchen könnten. ")]),_c('h3',{staticClass:"adsimple-111879084"},[_vm._v(" Warum verwenden wir reCAPTCHA auf unserer Website? ")]),_c('p',[_vm._v(" Wir wollen nur Menschen aus Fleisch und Blut auf unserer Seite begrüßen. Bots oder Spam-Software unterschiedlichster Art dürfen getrost zuhause bleiben. Darum setzen wir alle Hebel in Bewegung, uns zu schützen und die bestmögliche Benutzerfreundlichkeit für Sie anzubieten. Aus diesem Grund verwenden wir Google reCAPTCHA der Firma Google. So können wir uns ziemlich sicher sein, dass wir eine „botfreie“ Webseite bleiben. Durch die Verwendung von reCAPTCHA werden Daten an Google übermittelt, um festzustellen, ob Sie auch wirklich ein Mensch sind. reCAPTCHA dient also der Sicherheit unserer Webseite und in weiterer Folge damit auch Ihrer Sicherheit. Zum Beispiel könnte es ohne reCAPTCHA passieren, dass bei einer Registrierung ein Bot möglichst viele E-Mail-Adressen registriert, um im Anschluss Foren oder Blogs mit unerwünschten Werbeinhalten „zuzuspamen“. Mit reCAPTCHA können wir solche Botangriffe vermeiden. ")]),_c('h3',{staticClass:"adsimple-111879084"},[_vm._v(" Welche Daten werden von reCAPTCHA gespeichert? ")]),_c('p',[_vm._v(" reCAPTCHA sammelt personenbezogene Daten von Usern, um festzustellen, ob die Handlungen auf unserer Webseite auch wirklich von Menschen stammen. Es kann also die IP-Adresse und andere Daten, die Google für den reCAPTCHA-Dienst benötigt, an Google versendet werden. IP-Adressen werden innerhalb der Mitgliedstaaten der EU oder anderer Vertragsstaaten des Abkommens über den Europäischen Wirtschaftsraum fast immer zuvor gekürzt, bevor die Daten auf einem Server in den USA landen. Die IP-Adresse wird nicht mit anderen Daten von Google kombiniert, sofern Sie nicht während der Verwendung von reCAPTCHA mit Ihrem Google-Konto angemeldet sind. Zuerst prüft der reCAPTCHA-Algorithmus, ob auf Ihrem Browser schon Google-Cookies von anderen Google-Diensten (YouTube. Gmail usw.) platziert sind. Anschließend setzt reCAPTCHA ein zusätzliches Cookie in Ihrem Browser und erfasst einen Schnappschuss Ihres Browserfensters. ")]),_c('p',[_vm._v(" Die folgende Liste von gesammelten Browser- und Userdaten, hat nicht den Anspruch auf Vollständigkeit. Vielmehr sind es Beispiele von Daten, die nach unserer Erkenntnis, von Google verarbeitet werden. ")]),_c('ul',{staticClass:"adsimple-111879084"},[_c('li',{staticClass:"adsimple-111879084"},[_vm._v(" Referrer URL (die Adresse der Seite von der der Besucher kommt) ")]),_c('li',{staticClass:"adsimple-111879084"},[_vm._v("IP-Adresse (z.B. 256.123.123.1)")]),_c('li',{staticClass:"adsimple-111879084"},[_vm._v(" Infos über das Betriebssystem (die Software, die den Betrieb Ihres Computers ermöglicht. Bekannte Betriebssysteme sind Windows, Mac OS X oder Linux) ")]),_c('li',{staticClass:"adsimple-111879084"},[_vm._v(" Cookies (kleine Textdateien, die Daten in Ihrem Browser speichern) ")]),_c('li',{staticClass:"adsimple-111879084"},[_vm._v(" Maus- und Keyboardverhalten (jede Aktion, die Sie mit der Maus oder der Tastatur ausführen wird gespeichert) ")]),_c('li',{staticClass:"adsimple-111879084"},[_vm._v(" Datum und Spracheinstellungen (welche Sprache bzw. welches Datum Sie auf Ihrem PC voreingestellt haben wird gespeichert) ")]),_c('li',{staticClass:"adsimple-111879084"},[_vm._v(" Alle Javascript-Objekte (JavaScript ist eine Programmiersprache, die Webseiten ermöglicht, sich an den User anzupassen. JavaScript-Objekte können alle möglichen Daten unter einem Namen sammeln) ")]),_c('li',{staticClass:"adsimple-111879084"},[_vm._v(" Bildschirmauflösung (zeigt an aus wie vielen Pixeln die Bilddarstellung besteht) ")])]),_c('p',[_vm._v(" Unumstritten ist, dass Google diese Daten verwendet und analysiert noch bevor Sie auf das Häkchen „Ich bin kein Roboter“ klicken. Bei der Invisible reCAPTCHA-Version fällt sogar das Ankreuzen weg und der ganze Erkennungsprozess läuft im Hintergrund ab. Wie